<template>
    <b-table
        :items="players"
        :fields="fields"
        borderless
        striped
        responsive
        show-empty
        table-variant="secondary"
        class="mb-0 text-nowrap"
        td-class
    >
        <template v-slot:head()="data">
            <div v-if="data.column == 'chosen_mode.country_leaderboard_rank'">
                <router-link
                    :to="{ name: 'leaderboard', params:{ mode: mode ? mode : undefined, rx: rx ? rx : undefined}}"
                >
                    <gb-flag :code="country" size="small" icon-path="assets/flags" class="pr-2" />
                </router-link>
            </div>
            <div v-else>{{$t(`leaderboard.${data.label}`)}}</div>
        </template>
        <template v-slot:cell(user)="data">
            <div class="d-flex">
                <router-link
                    :to="{ name: 'country-leaderboard', params:{country:data.item.country, mode: mode ? mode : undefined, rx: rx ? rx : undefined}}"
                >
                    <gb-flag
                        :code="data.item.country"
                        size="small"
                        icon-path="assets/flags"
                        class="mr-2"
                        v-if="country == ''"
                    />
                </router-link>
                <div class="d-flex">
                    <table-user :user="data.item" :mode="mode ? mode : undefined" :rx="rx ? rx : undefined" />
                </div>
            </div>
        </template>
    </b-table>
</template>
<script>
import TableUser from "@/views/components/sb/components/TableUser";
const osu = require("node-osu");
const humanFormat = require("human-format");

import modHelper from "@/helpers/enum.js";
export default {
    components: {
        // LazyImage,
        TableUser
    },
    props: {
        players: {
            type: Array,
            default: () => []
        },
        country: {
            type: String,
            default: ""
        },
        mode: {
            type: String,
            default: ""
        },
        rx: {
            type: String,
            default: ""
        }
    },
    data() {
        return {
            logger: false,
            StaticRows: [
                {
                    label: "user",
                    key: "user"
                },
                {
                    label: "accuracy",
                    key: "chosen_mode.accuracy",
                    formatter: k =>
                        (parseFloat(k) / 100).toLocaleString("en", {
                            style: "percent",
                            minimumSignificantDigits: 2,
                            maximumSignificantDigits: 4
                        }),
                    thClass: ["text-right"],
                    tdClass: ["text-right"]
                },
                {
                    label: "rankedScore",
                    key: "chosen_mode.ranked_score",
                    formatter: k =>
                        k.toLocaleString("en", {
                            style: "decimal"
                        }),
                    thClass: ["text-right"],
                    tdClass: ["text-right"]
                },
                {
                    label: "totalScore",
                    key: "chosen_mode.total_score",
                    formatter: k =>
                        k.toLocaleString("en", {
                            style: "decimal"
                        }),
                    thClass: ["text-right"],
                    tdClass: ["text-right"]
                },
                {
                    label: "pp",
                    key: "chosen_mode.pp",
                    formatter: k =>
                        `${k.toLocaleString("en", {
                            style: "decimal"
                        })}pp`,
                    thClass: ["text-right"],
                    tdClass: ["text-right"]
                }
            ]
        };
    },
    mounted() {
        this.init();
    },
    computed: {
        bid() {
            return this.beatmap.id;
        },
        fields() {
            return [
                {
                    label: "index",
                    // flag: this.country,
                    key: this.country
                        ? "chosen_mode.country_leaderboard_rank"
                        : "chosen_mode.global_leaderboard_rank",
                    formatter: k => `#${k}`,
                    thClass: ["text-right"],
                    tdClass: ["text-right"]
                },
                ...this.StaticRows
            ];
        }
    },
    methods: {
        async init() {
            this.log(this.fields, "fields");
        },
        smallerFloatHtml(str) {
            const splitted = str.split(".");
            return `${splitted[0]}<small class="float-number">.${splitted[1]}</small></span>`;
        },
        htmlCounts(counts) {
            const c = [
                // { count: this.score.count_300, append: "x 300" },
                { count: counts[100], append: "x 100" },
                { count: counts[50], append: "x 50" },
                { count: counts.miss, append: "Miss" }
            ]
                .filter(({ count }) => count)
                .map(
                    ({ count, append }) =>
                        `${count} <small class="float-number">${append}</small>`
                )
                .join(", ");
            return c;
        },
        humanFormat(...any) {
            return humanFormat(...any);
        }
    }
};
</script>

