<template>
    <div>
        <!-- <profile-layout> -->
        <!-- <top-section-layout v-if="ready">
            <beatmap-info :beatmap="current">
                <template v-slot:diff-switcher>
                    <b-tabs v-model="diffIndex" pills>
                        <b-tab
                            v-for="(bm) in set"
                            :key="`diff-switch-${bm.id}`"
                            title-item-class="mb-2 pr-2"
                        >
                            <template v-slot:title class="nav-link-icon">{{ bm.version }}</template>
                        </b-tab>
                    </b-tabs>
                </template>
            </beatmap-info>
        </top-section-layout>-->
        <section-layout section-class="section-lg" shaped bg-variant="dark">
            <card shadow>
                <mode-switcher v-model="numericMode" />
            </card>
        </section-layout>
        <section-layout shaped container-class="py-0">
            <card no-body shadow>
                <!-- <base-pagination :page-count="10" align="center"></base-pagination> -->
                <leaderboard-table :players="currentPagePlayers" :country="country ? country : undefined" :mode="mode ? mode : undefined" :rx="rx ? rx : undefined" />
                <base-pagination
                    :per-page="perPage"
                    :total="1000"
                    align="center"
                    v-model="leaderboardCurrentPage"
                    class="my-2"
                ></base-pagination>
            </card>
        </section-layout>
        <!-- </profile-layout> -->
    </div>
</template>
<script>
import LeaderboardTable from "@/views/components/sb/LeaderboardTable";
import ModeSwitcher from "@/views/components/sb/ModeSwitcher";
import SectionLayout from "@/layout/components/SectionLayout";

const axios = require("axios");

export default {
    components: {
        SectionLayout,
        ModeSwitcher,
        LeaderboardTable
    },
    data: () => ({
        logger: true,
        ready: false,
        fetchedPage: {},
        perPage: 50,
        leaderboardCurrentPage: 1
        // m: 0
    }),
    props: ["mode", "rx", "country"],
    head: {
        title: function() {
            const separator = " > ";
            const arr = ["Leaderboard"];
            // arr.push(this.songName || "loading");
            const inner = arr.join(separator);
            return {
                inner
            };
        }
    },
    watch: {
        $route() {
            this.init();
        }
    },
    async mounted() {
        await this.init();
    },
    computed: {
        numericMode: {
            get() {
                return this.toNumericalMode(this.mode);
            },
            set(newVal) {
                this.$router.push({
                    name: this.country ? "country-leaderboard" : "leaderboard",
                    params: {
                        mode: this.toStrMode(newVal),
                        rx: this.rx ? this.rx : undefined,
                        country: this.country
                    }
                });
            }
        },
        strMode() {
            return this.toStrMode();
        }
    },
    asyncComputed: {
        async currentPagePlayers() {
            return await this.fetchPage();
        }
    },
    methods: {
        async init() {
            this.log(this.$props, "props");
            this.fetchedPage = {};
        },
        async fetchPage(page = this.leaderboardCurrentPage) {
            if (
                this.fetchedPage[page] &&
                typeof this.fetchedPage[page] == "array"
            )
                return this.fetchedPage[page];
            this.ready = false;
            // this.log({ page }, "fetch-page");
            const players = await axios({
                url: "https://osu.ppy.sb/api/v1/leaderboard", //?mode=0&p=1&l=50&country=
                params: {
                    mode: this.numericMode,
                    p: page,
                    l: this.perPage,
                    rx: this.rx ? true : false,
                    country: this.country
                        ? this.country.toLowerCase()
                        : undefined
                }
            })
                .then(res => res.data)
                .then((res, reject) =>
                    res.code == 200 ? res.users : reject("code returns false")
                )
                .catch(err => undefined);
            this.fetchedPage[page] = players;
            this.ready = true;
            return players;
        },
        toNumericalMode(mode = this.mode) {
            switch (mode) {
                case "std":
                case "osu":
                    return 0;
                case "taiko":
                    return 1;
                case "catch":
                case "ctb":
                    return 2;
                case "mania":
                    return 3;
                default:
                    return parseInt(mode) == NaN ? 0 : parseInt(mode);
            }
        },
        toStrMode(numericMode = this.numericMode) {
            switch (numericMode) {
                case 0:
                    return "std";
                case 1:
                    return "taiko";
                case 2:
                    return "ctb";
                case 3:
                    return "mania";
                default:
                    break;
            }
            // return new Promise([[0,'std'],[0,'osu'],[1,'taiko'],[2,'catch'],[2,'ctb'],[3,'mania']].find(([n,s]) => n == numericMode)).then(([n,s]) => s);
        }
    }
};
</script>
<style scoped>
.nav-item {
    padding-top: 1rem;
}
