<template>
    <profile-layout>
        <top-section-layout v-if="ready">
            <beatmap-info :beatmap="currentMap">
                <template v-slot:diff-switcher>
                    <b-tabs v-model="diffIndex" pills>
                        <b-tab
                            v-for="(bm) in set"
                            :key="`diff-switch-${bm.id}`"
                            title-item-class="mb-2 pr-2"
                        >
                            <template v-slot:title class="nav-link-icon">{{ bm.version }}</template>
                        </b-tab>
                    </b-tabs>
                </template>
            </beatmap-info>
        </top-section-layout>
        <section-layout v-if="ready" section-class="pt-0" container-class="pt-0">
            <card shadow no-body v-if="osuWebCompatible" class="border-0">
                <!-- <b-embed
                    style="border-radius: 0.25rem;"
                    type="iframe"
                    aspect="4by3"
                    :src="`https://osugame.online/preview.html?sid=${beatmapSetId}&bid=${beatmapId}`"
                    allowfullscreen
                ></b-embed> -->
                <beatmap-preview :beatmapId="beatmapId" :beatmapSetId="beatmapSetId"></beatmap-preview>
            </card>
            <card shadow v-else>
                Safari is not compatible with preview.
            </card>
        </section-layout>
        <section-layout v-if="ready" section-class="py-0" container-class="pt-0">
            <card shadow>
                <mode-switcher v-model="numericMode" />
            </card>
        </section-layout>
        <section-layout v-if="ready" bg-variant="success" skew shaped>
            <beatmap-scores
                :scores="currentMapScores"
                :mode="mode ? mode : undefined"
                :rx="rx ? rx : undefined"
            />
        </section-layout>
    </profile-layout>
</template>
<script>
import ModeSwitcher from "@/views/components/sb/ModeSwitcher";
import BeatmapInfo from "@/views/components/sb/BeatmapInfo";
import BeatmapScores from "@/views/components/sb/BeatmapScores";
import SectionLayout from "@/layout/components/SectionLayout";
import TopSectionLayout from "@/layout/components/TopSectionLayout";
import ProfileLayout from "@/layout/ProfileLayout";
import BeatmapPreview from "@/components/osu/Preview";

const osu = require("node-osu");

export default {
    components: {
        ModeSwitcher,
        BeatmapInfo,
        BeatmapScores,
        TopSectionLayout,
        SectionLayout,
        ProfileLayout,
        BeatmapPreview
    },
    data: () => ({
        logger: true,
        set: [],
        // beatmapId: 0,
        api: undefined,
        ready: false,
        modeScores: {
            0: {},
            1: {},
            2: {},
            3: {}
        }
    }),
    props: ["beatmapSetId", "beatmapId", "mode", "rx"],
    watch: {
        // $route(to, from) {
        //     this.log({ new: to.params, now: {bid: this.beatmapId,bsid: this.beatmapSetId} });
        //     if (to.params.beatmapSetId !== from.params.beatmapSetId) {
        //         this.reload();
        //     }
        // }
        async beatmapSetId() {
            this.log("switch set", "logging");
            await this.init();
            this.$emit("updateHead");
        },
        async beatmapId() {
            this.$emit("updateHead");
        },
        async mode() {
            this.$emit("updateHead");
            // await this.init();
        }
    },
    head: {
        title: function() {
            const separator = " > ";
            const arr = [this.ready ? this.currentMap.version : "loading"];
            arr.push(this.songName || "loading");
            const inner = arr.join(separator);
            return {
                inner
            };
        }
    },
    async mounted() {
        this.log(
            ["beatmapSetId", "beatmapId", "mode", "rx"].reduce((acc, cur) => {
                acc[cur] = this[cur];
                return acc;
            }, {}),
            "variables"
        );
        if (this.beatmapId == null && this.beatmapSetId == null) {
            this.log("parameter wrong..", "error", { variant: "error" });
        }
        await this.init();
        this.$emit("updateHead");
    },
    computed: {
        osuWebCompatible(){
            return !this.$browserDetect.isSafari
        },
        currentMap: {
            get() {
                return this.set.find(bm => bm.id == this.beatmapId);
            }
        },
        numericMode: {
            get() {
                switch (this.mode) {
                    case "std":
                    case "osu":
                        return 0;
                    case "taiko":
                        return 1;
                    case "catch":
                    case "ctb":
                        return 2;
                    case "mania":
                        return 3;
                    default:
                        return 0;
                }
            },
            set(newVal) {
                this.$router.push({
                    name: "beatmapsets",
                    params: {
                        beatmapSetId: this.beatmapSetId,
                        beatmapId: this.beatmapId,
                        mode: this.toStrMode(newVal),
                        rx: this.rx ? this.rx : undefined
                    }
                });
            }
        },
        scores() {
            return this.modeScores[this.numericMode];
        },
        songName() {
            if (this.ready)
                return `${this.currentMap.artist} - ${this.currentMap.title}`;
            else return "";
        },
        bsid() {
            if (this.beatmapSetId) return this.beatmapSetId;
            else if (this.currentMap) return this.currentMap.beatmapSetId;
            else if (this.set.length > 0) return this.set[0].beatmapSetId;
            else return -1;
        },
        diffIndex: {
            get() {
                return this.set.findIndex(bm => bm.id == this.beatmapId) || 0;
            },
            set(newVal) {
                // this.beatmapId = this.set[newVal].id;
                this.$router.push({
                    name: "beatmapsets",
                    params: {
                        beatmapSetId: this.beatmapSetId,
                        beatmapId: this.set[newVal].id
                    }
                });
            }
        }
    },
    asyncComputed: {
        currentMapScores: {
            async get() {
                if (this.ready) {
                    // this.log(this.currentMap, "currentMap in asyncComputed");
                    return await this.getScoresById(this.currentMap.id);
                }
                return [];
            }
        }
    },
    methods: {
        async init() {
            (this.ready = false),
                (this.api = new osu.Api("A3tGREAemXk213gfJJUewH9675g", {
                    baseUrl: this.$root.peppyApiBase,
                    parseNumeric: true // Parse numeric values into numbers/floats, excluding ids
                }));
            await this.getMaps();
            await this.setBeatmapId();
            this.ready = true;
            // this.log(this.currentMap, "currentMap-map");
        },
        async getMaps() {
            if (this.beatmapSetId) {
                await this.getSetMaps();
            } else if (this.beatmapId) {
                const map = await this.getMapFromId().then(maps => maps[0]);
                if (map.beatmapSetId) {
                    this.beatmapSetId = map.beatmapSetId;
                    this.set.push(map);
                    // delayed loading (without await)(showing loaded single beatmap while loading sets)
                    this.getSetMaps();
                }
            }
        },
        async getSetMaps() {
            const result = await this.api.getBeatmaps({
                s: this.beatmapSetId
            });
            result.sort((a, b) => a.difficulty.rating - b.difficulty.rating);
            // this.log(result);
            this.set = result;
        },
        async getMapFromId(id = this.beatmapId) {
            return await this.api.getBeatmaps({
                b: id
            });
        },
        async getScoresById(id = this.beatmapId) {
            if (this.scores[id]) return this.scores[id];
            else
                return await this.fetchScores({
                    b: id,
                    m: this.numericMode,
                    limit: 100
                });
        },
        safariCanNotParseMysqlDateTimeWhy(dateTime) {
            // example MySQL DATETIME
            // const dateTime = '2017-02-04 11:23:54';

            let dateTimeParts = dateTime.split(/[- :]/); // regular expression split that creates array with: year, month, day, hour, minutes, seconds values
            dateTimeParts[1]--; // monthIndex begins with 0 for January and ends with 11 for December so we need to decrement by one

            const dateObject = new Date(...dateTimeParts); // our Date object
            // console.log(dateObject);
            return dateObject;
        },
        async fetchScores(f) {
            try {
                let result = await this.api.getScores(f);
                result = result.map(score => {
                    const detached = JSON.parse(JSON.stringify(score));
                    score._beatmap = this.currentMap;
                    ["accuracy", "mods", "date"].map(
                        getter => (detached[getter] = score[getter])
                    );
                    if (isNaN(detached.date.getTime()))
                        detached.date = this.safariCanNotParseMysqlDateTimeWhy(
                            detached.raw_date
                        );
                    return detached;
                });
                this.scores[f.b] = result;
                return result;
            } catch (e) {
                this.log(e, "error on get scores", { variant: "danger" });
                return [];
            }
        },
        async setBeatmapId() {
            this.beatmapId = this.beatmapId;
            if (this.beatmapId == null) {
                if (this.set.length > 0) {
                    this.beatmapId = this.set[0].id;
                }
            }
        },
        toStrMode(numericMode = this.numericMode) {
            switch (numericMode) {
                case 0:
                    return "std";
                case 1:
                    return "taiko";
                case 2:
                    return "ctb";
                case 3:
                    return "mania";
                default:
                    break;
            }
        }
    }
};
</script>
<style scoped>
.nav-item {
    padding-top: 1rem;
}
</style>